.footer-container {
  background-color: var(--color-black);
  padding: 2rem 1rem;
  font-size: 0.95rem;
  color: white;
  font-weight: 200;
  border-top: 3px solid var(--color-white-alpha-020);
}

.footer-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.footer-box span {
  display: block;
  font-weight: 200;
  margin: 0 auto;
}

.footer-email {
  color: var(--color-red-light);
  font-weight: 700; 
}

/* social media */

.footer-social-media {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 1rem auto 1rem auto;
}

.footer-footer > * {
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.footer-icon {
  font-size: 2.25rem;
  text-align: center;
  padding: 0 0.2rem;
}

.footer-icon a {
  text-decoration: none;
}

.footer-icon a svg {
  color: white;
  margin: 0 0.75rem;
}

.footer-icon.facebook:hover * *, 
.footer-icon.facebook a:hover * * {
  fill: var(--color-facebook);
}

.footer-icon.instagram:hover * *, 
.footer-icon.instagram a:hover * * {
  fill: var(--color-instagram);
}

.footer-icon.medium:hover * *, 
.footer-icon.medium a:hover * * {
  fill: var(--color-medium);
}

.footer-icon.twitter:hover * *, 
.footer-icon.twitter a:hover * * {
  fill: var(--color-twitter);
}

/* copyright box */

.footer-logo {
  margin: 0.75rem auto;
  max-width: 17.5%;
}

@media (max-width: 599px) {
  .footer-logo {
    max-width: 30%;
  }
}

@media (min-width: 600px) and (max-width: 767px) {
  .footer-logo {
    max-width: 25%;
  }
}

@media (min-width: 768px) {
  .footer-icon a svg {
    margin: 0 1rem;
  }
}



