.faq-card-container {
  margin: 1rem;
  border-radius: 0.2rem;
  border: 1px solid var(--color-black-alpha-020);
}

.faq-card-container:first-of-type {
  margin-top: 1.5rem;
}

.faq-card-container:last-of-type {
  margin-bottom: 1.5rem;
}

.faq-card-container.open {
  -webkit-box-shadow: 0 1px 6px 0 var(--color-box-shadow); 
  box-shadow: 0 1px 6px  var(--color-box-shadow);
}

.faq-card-summary {
  text-align: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 1rem 0.5rem 1rem 1rem;
  background-color: var(--color-black-alpha-003);
  cursor: pointer;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.faq-card-summary:hover {
  background-color: var(--color-black-alpha-010);
}

.faq-card-summary span {
  font-weight: 600;
}

.faq-card-icon-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 2.5rem;
  width: 3rem;
  padding-right: 0.5rem;
  padding-left: 1rem;
  font-size: 1.3rem;
}

.faq-card-icon {
  color: var(--color-red);
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.faq-card-container.open .faq-card-icon {
  -webkit-transform: rotate(-180deg);
      -ms-transform: rotate(-180deg);
          transform: rotate(-180deg)
}

.faq-card-content {
  text-align: justify;
  text-justify: inter-word;
  padding: 0 1rem;
  max-height: 0;
  overflow: hidden;
  -webkit-transition: max-height 0.5s ease;
  -o-transition: max-height 0.5s ease;
  transition: max-height 0.5s ease;
}

.faq-card-content p {
  margin-top: 1rem;
}

@media (min-width: 768px) {
  .faq-card-container {
    margin: 2rem;
  }

  .faq-card-container:first-of-type {
    margin-top: 3rem;
  }

  .faq-card-container:last-of-type {
    margin-bottom: 3rem;
  }
}

@media (hover: none) {
  .faq-card-summary {
    background-color: var(--color-black-alpha-005);
  }
}


