:root {
  --background-image-size: 80px;
}

.loading-container {
  /* display: none; */
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: var(--color-grey-lighter);
  z-index: var(--z-index-level-ceil);
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.loading-container.hidden {
  display: none;
}

.loading-container.visually-hidden {
  opacity: 0;
}

.loading-image-container {
  background-image: url("../../assets/africa_print.png");
  background-size: var(--background-image-size);
  width: var(--background-image-size);
  height: var(--background-image-size);
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-animation: loading 2s linear;
  animation: loading 2s linear;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.loading-image-container img {
  display: block;
  -o-object-fit: cover;
     object-fit: cover;
}

@media (min-width: 400px) {
  :root {
    --background-image-size: 100px;
  }
}


@media (min-width: 768px) {
  :root {
    --background-image-size: 120px;
  }
}


@media (min-width: 1200px) {
  :root {
    --background-image-size: 140px;
  }
}

@-webkit-keyframes loading {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1) 
  }

  20% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2) 
  }

  40% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8) 
  }

  60% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
  }

  100% {
    -webkit-transform: scale(0);
            transform: scale(0) 
  }
}

@keyframes loading {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1) 
  }

  20% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2) 
  }

  40% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8) 
  }

  60% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
  }

  100% {
    -webkit-transform: scale(0);
            transform: scale(0) 
  }
}


